import $ from "jquery";
import slick from "slick-carousel";

// slick-carousel bem block "reviews"


$('#reviews-slider').slick({
  autoplay: false,
  autoplaySpeed: 3000,
  dots: true,
  arrows: true,
  infinite: true,
  slidesToShow: 1,
  speed: 300,
  fade: true,
  responsive: [
    {
      breakpoint: 768,
      settings: {
        arrows : false
      }
    }, 
  ]
});