import $ from "jquery";

// Переменные модальное окно "Логин"
var link = document.querySelector(".user-list__login");
var popup = document.querySelector(".modal-login");
var bgLogin = document.querySelector(".modal-overlay");
var loginClose = popup.querySelector(".modal-close");
var login = popup.querySelector("[name=login]");
var password = popup.querySelector("[name=password]");
var storage = localStorage.getItem("login");
var form = popup.querySelector("form");

// Модальное окно "Логин"
link.addEventListener("click", function (evt) {
    evt.preventDefault();
    popup.classList.add("modal--show");
    bgLogin.classList.add("modal-overlay--show");
    login.focus();
    if (storage) {
        login.value = storage;
    }
});
loginClose.addEventListener("click", function (evt) {
    evt.preventDefault();
    popup.classList.remove("modal--show");
    bgLogin.classList.remove("modal-overlay--show");
    popup.classList.remove("modal--error");
});
