import $ from "jquery";
import slick from "slick-carousel";

// slick-carousel bem block "advantages"
var $team_slider = $('#advantages-slider')
var team_slider_settings = {
  autoplay: true,
  autoplaySpeed: 3000,
  dots: true,
  arrows: false,
  infinite: true,
  slidesToShow: 1,
  speed: 300,
  fade: true,
  responsive: [
    {
      breakpoint: 99999,
      settings: "unslick"
    },{
      breakpoint: 768,
      settings: {
        slidesToShow: 1,
      }
    }
  ]          
};

// init slider
$team_slider.slick(team_slider_settings);

// reslick only if it's not slick()
$(window).on('resize', function() {
  if (!$team_slider.hasClass('slick-initialized')) {
    return $team_slider.slick(team_slider_settings);
  }
});